import React from "react";
import design from "../Navbar.module.css"
import { NavLink } from 'react-router-dom'

class ListPages extends React.Component {
    constructor(props) {
        super(props);
    }
    render() {
        var setActive = ({ isActive }) => ({ color: isActive ? '#404f80' : '#fff' });
        return (
            <div className={design.navbar_list_pages}>
                <NavLink to="/profile" style={setActive}>Profile</NavLink>
                <NavLink to="/dialogs" style={setActive}>Dialogs</NavLink>
                <NavLink to="/friends" style={setActive}>Friends</NavLink>
            </div>
        )
    }

}

export default ListPages;