import { authAPI } from '../ajax/api'

const SET_AUTH_USER = 'SET_AUTH_USER';
const SET_LOGINNED_AUTH_STATUS = 'SET_LOGINNED_AUTH_STATUS';
const SET_UNLOGINNED_AUTH_STATUS = 'SET_UNLOGINNED_AUTH_STATUS';
const SET_CAPTCHA = 'SET_CAPTCHA';
const SET_ERROR = 'SET_ERROR';

let initialState = {
    email: null,
    userId: null,
    login: null,
    captcha: '',
    isAuth: false,
    errorMessage: ''
}

export let loginReducer = (state = initialState, action) => {
    switch (action.type) {
        case SET_AUTH_USER:
            return {
                ...state,
                ...action.data
            }
        case SET_LOGINNED_AUTH_STATUS:
            return {
                ...state,
                isAuth: true
            }
        case SET_UNLOGINNED_AUTH_STATUS:
            return {
                ...state,
                isAuth: false
            }
        case SET_CAPTCHA:
            return {
                ...state,
                captcha: action.captchaURL
            }
        case SET_ERROR:
            return {
                ...state,
                errorMessage: action.message
            }
        default:
            return state
    }
}

export const SetAuthUser = (email, userId, login) => ({ type: SET_AUTH_USER, data: { email, userId, login } })
export const SetLoginnedAuthStatus = () => ({ type: SET_LOGINNED_AUTH_STATUS });
export const SetUnloginnedAuthStatus = () => ({ type: SET_UNLOGINNED_AUTH_STATUS });
export const SetLoginCaptcha = (captchaURL) => ({ type: SET_CAPTCHA, captchaURL })
export const SetErrorMessage = (message) => ({ type: SET_ERROR, message })

export const authUser = () => async (dispatch) => {
    let response = await authAPI.authAccount()
    if (response.resultCode === 0) {
        let { email, id, login } = response.data
        dispatch(SetAuthUser(email, id, login))
        dispatch(SetLoginnedAuthStatus())
    }
}


export const loginAccount = (email, password, rememberMe, captcha) => async (dispatch) => {
    let response = await authAPI.loginAccount(email, password, rememberMe, captcha)
    if (response.data.resultCode === 0) {
        dispatch(authUser())
    }
    else if (response.data.resultCode === 10) {
        authAPI.getCaptchaURL().then(response => {
            dispatch(SetLoginCaptcha(response.data.url))
        })
        dispatch(SetErrorMessage(response.data.messages[0]));
    }
    else {
        authAPI.getCaptchaURL().then(response => {
            dispatch(SetLoginCaptcha(response.data.url))
        })
        dispatch(SetErrorMessage(response.data.messages[0]));
    }
}

export const logoutAccount = () => async (dispatch) => {
    let response = await authAPI.logoutAccount()
    if (response.data.resultCode === 0) {
        dispatch(SetAuthUser(null, null, null))
        dispatch(SetLoginCaptcha(''))
        dispatch(SetUnloginnedAuthStatus())
        dispatch(SetErrorMessage(''));
    }
}