import { authUser } from './login-reducer';

const SET_INITIALIZED_RESPONSE = 'SET_INITIALIZED_RESPONSE';

let initialState = {
    initialized: false
}

export let appReducer = (state = initialState, action) => {
    switch (action.type) {
        case SET_INITIALIZED_RESPONSE:
            return {
                ...state,
                initialized: action.initialize
            }
        default:
            return state
    }
}

export const SetInitializedResponse = (initialize) => ({ type: SET_INITIALIZED_RESPONSE, initialize })

export const initialize = (initialize) => (dispatch) => {
    let promise = dispatch(authUser());

    Promise.all([promise]).then(() => {
        dispatch(SetInitializedResponse(initialize))
    })
}