import axios from 'axios';


const instance = axios.create({
    baseURL: 'https://social-network.samuraijs.com/api/1.0/',
    withCredentials: true,
    headers: { 'API-KEY': '856b9fed-2542-4ce3-bf6e-11999eb54903' }
})

export const profileAPI = {
    putNewprofilePhoto(photo) {
        var formData = new FormData();
        formData.append("image", photo);
        return instance.put(`/profile/photo`, formData, {
            headers: {
                'Content-Type': 'multipart/form-data'
            }
        })
    },

    getProfile(userId) {
        return instance.get(`profile/` + userId)
    },

    getProfileStatus(userId) {
        return instance.get(`profile/status/` + userId)
    },
    updateProfileStatus(status) {
        return instance.put(`profile/status`, { status })
    }
}

export const friendsAPI = {
    getUsers(usersForPage, currentPage) {
        return instance.get(`users?count=${usersForPage}&page=${currentPage}`).then(response => response.data)
    },

    followFriendRequest(userId) {
        return instance.post(`follow/` + userId).then(response => response.data)
    },

    unfollowFriendRequest(userId) {
        return instance.delete(`follow/` + userId, { userId: userId }).then(response => response.data)
    }
}

export const authAPI = {
    authAccount() {
        return instance.get(`auth/me`).then(response => {
            return response.data
        })
    },
    loginAccount(email, password, rememberMe, captcha) {
        return instance.post(`auth/login`, { email, password, rememberMe, captcha })
    },
    logoutAccount() {
        return instance.delete(`auth/login`)
    },
    getCaptchaURL() {
        return instance.get(`/security/get-captcha-url`)
    }
}
