import { profileAPI } from '../ajax/api'

const ADD_POST = "ADD-POST";
const SET_USER_PROFILE = "SET_USER_PROFILE";
const SET_PROFILE_STATUS = 'SET_PROFILE_STATUS';
const SET_NEW_USER_PHOTO = 'SET_NEW_USER_PHOTO';

let initialState = {
    profileWallpaper: { src: "https://marketplace.canva.com/EAFHm4JWsu8/1/0/1600w/canva-pink-landscape-desktop-wallpaper-HGxdJA_xIx0.jpg" },
    profileLogo: { src: "https://img.freepik.com/free-icon/profile_318-554048.jpg" },
    profileName: { name: "John Pery" },
    posts: [
        { message: "Haha", postId: 1 },
        { message: "Dada", postId: 2 },
        { message: "Nene", postId: 3 },
        { message: "Hi", postId: 4 },
    ],
    profile: null
}

export let profileReducer = (state = initialState, action) => {
    switch (action.type) {
        case ADD_POST:
            let newPost = {
                id: 5,
                message: action.newPostText
            }
            return {
                ...state,
                posts: [...state.posts, newPost]
            }
        case SET_USER_PROFILE:
            return {
                ...state,
                profile: action.userInfo
            }
        case SET_PROFILE_STATUS:
            return {
                ...state,
                profileStatus: action.statusText
            }
        case SET_NEW_USER_PHOTO:
            return{
                ...state,
                profile: { ...state.profile, photos:action.newProfileLogo }
            }
        default:
            return state;
    }
}

export const addPostActionCreator = (newPostText) => ({ type: ADD_POST, newPostText });
export const SetUserProfile = (userInfo) => ({ type: SET_USER_PROFILE, userInfo })
export const SetProfileStatus = (statusText) => ({ type: SET_PROFILE_STATUS, statusText })
export const SetNewUserPhoto = (newProfileLogo) => ({ type: SET_NEW_USER_PHOTO, newProfileLogo}) 


export const requestProfile = (userId) => async (dispatch) => {
    let response = await profileAPI.getProfile(userId)
    dispatch(SetUserProfile(response.data))
}

export const requestProfileStatus = (userId) => async (dispatch) => {
    let response = await profileAPI.getProfileStatus(userId)
    dispatch(SetProfileStatus(response.data))
}

export const updateProfileStatus = (status) => async (dispatch) => {
    let response = await profileAPI.updateProfileStatus(status)
    if (response.resultCode === 0) {
        dispatch(SetProfileStatus(status))
    }
}

export const putNewProfileLogo = (photo) => async (dispatch) => {
    let response = await profileAPI.putNewprofilePhoto(photo)
    if(response.data.resultCode === 0){
        dispatch(SetNewUserPhoto(response.data.data.photos))
    }
}