import React from "react";
import ListPages from "./ListPages/ListPages";
import design from "./Navbar.module.css"

class Navbar extends React.Component {
    constructor(props) {
        super(props);
        this.state={
            burgerMenu: false
        }
    }

    changeBurgerMenuStyle = () => {
        this.setState({
            burgerMenu: !this.state.burgerMenu
        })
    }

    render() {
        return (
            <>
                <div className={design.navbar}>
                    <ListPages />
                </div>
                <div className={design.navbar_burger} onClick={this.changeBurgerMenuStyle}>
                    {this.state.burgerMenu ? <i className="fa fa-times" aria-hidden="true"></i> :<i className="fa fa-bars" aria-hidden="true"></i>}
                    <div className={design.burger_menu} style={{ display:this.state.burgerMenu ? "block" : "none"}}><ListPages /></div>
                </div>
            </>
        )
    }

}

export default Navbar;