import { friendsAPI } from "../ajax/api";

const FOLLOW_UNFOLLOW_REQUESTS = 'FOLLOW_UNFOLLOW_REQUESTS';
const SET_USERS = 'SET_USERS';
const SET_USERS_QUANTITY = 'SET_USERS_QUANTITY';
const SET_CURRENT_PAGE = 'SET_CURRENT_PAGE';
const SET_BUTTON_REQUEST = 'SET_BUTTON_REQUEST';

let initialState = {
    friendsList: [],
    allUsers: null,
    usersForPage: 10,
    currentPage: 1,
    buttonRequest: []
}

export let friendsReducer = (state = initialState, action) => {
    switch (action.type) {
        case FOLLOW_UNFOLLOW_REQUESTS:
            return {
                ...state,
                friendsList: state.friendsList.map(friendId => {
                    if (friendId.id === action.id) {
                        return { ...friendId, followed: action.followed }
                    }
                    return friendId
                })
            }
        case SET_USERS:
            return {
                ...state,
                friendsList: action.users,
            }
        case SET_USERS_QUANTITY:
            return {
                ...state,
                allUsers: action.quantity
            }
        case SET_CURRENT_PAGE:
            return {
                ...state,
                currentPage: action.currentPage
            }
        case SET_BUTTON_REQUEST:
            return {
                ...state,
                buttonRequest: action.Fetching ? [...state.buttonRequest, action.userId] : state.buttonRequest.filter(id => id !== action.userId)
            }
        default:
            return state;
    }
}

export const FollowUnfollowFriend = (id, followed) => ({ type: FOLLOW_UNFOLLOW_REQUESTS, id, followed });
export const SetUsers = (users) => ({ type: SET_USERS, users });
export const SetQuantityOfUsers = (quantity) => ({ type: SET_USERS_QUANTITY, quantity });
export const SetCurrentPage = (currentPage) => ({ type: SET_CURRENT_PAGE, currentPage });
export const SetButtonRequest = (Fetching, userId) => ({ type: SET_BUTTON_REQUEST, Fetching, userId });

export const getFriends = (usersForPage, currentPage) => async (dispatch) => {
    let response = await friendsAPI.getUsers(usersForPage, currentPage)
    dispatch(SetUsers(response.items));
    dispatch(SetQuantityOfUsers(response.totalCount));
    dispatch(SetCurrentPage(currentPage))
}

export const FollowUnfollowRequest = (userId, setBoolean) => async (dispatch) => {
    dispatch(SetButtonRequest(true, userId))
    let response = setBoolean && await friendsAPI.followFriendRequest(userId) || !setBoolean && await friendsAPI.unfollowFriendRequest(userId)
    if (response.resultCode === 0) {
        dispatch(FollowUnfollowFriend(userId, setBoolean))
    }
    dispatch(SetButtonRequest(false, userId))
}