import React from "react";
import { connect } from "react-redux";
import Header from "./Header";
import { logoutAccount } from "../redux/login-reducer";
import { getProfilePhoto, getUserLogin } from "../redux/login-selectors";

class HeaderContainer extends React.Component {
    constructor(props) {
        super(props);
    }

    render() {
        return (
           <Header profilePhoto={this.props.profilePhoto} userLogin={this.props.userLogin} logoutAccount={this.props.logoutAccount} />
        )
    }

}


const mapStateToProps = (state) => {
    return {
        userLogin: getUserLogin(state),
        profilePhoto: getProfilePhoto(state)
    }
}

export default connect(mapStateToProps, {logoutAccount})(HeaderContainer);