import React, { Suspense } from 'react';
import { connect } from 'react-redux';
import { BrowserRouter, Route, Routes } from 'react-router-dom';
import { compose } from 'redux';
import './App.css';
import { initialize } from './redux/app-reducer'
import HeaderContainer from './Header/HeaderContainer';
import Loader from './assets/Loader/Loader';
import Navbar from './Navbar/Navbar';
const LoginPage = React.lazy(() => import('./Login/LoginPage/LoginPage'));
const DialogsContainer = React.lazy(() => import('./Dialogs/DialogsContainer'));
const ProfileContainer = React.lazy(() => import('./Profile/ProfileContainer'));
const Friends = React.lazy(() => import('./Friends/FriendsContainer.jsx'))

class App extends React.Component {
  constructor(props) {
    super(props)
  }

  componentDidMount() {
    this.props.initialize(true);
  }

  componentDidUpdate(prevProps) {
    if (prevProps.userLogin !== this.props.userLogin) {
      this.props.authUser();
    }
  }

  render() {
    if(!this.props.initialized){
      return <Loader />
    }

    return (
      <BrowserRouter>
        <div className="app_wrapper">
          <HeaderContainer />
          <Navbar />
          <Suspense fallback={<Loader />}>
            <Routes>
              <Route path='/login' element={<LoginPage />} />
              <Route path='/profile/:userId?' element={<ProfileContainer profilePage={this.props.profilePage} />} />
              <Route path='/dialogs' element={<DialogsContainer messagePage={this.props.messagePage} />} />
              <Route path='/friends' element={<Friends />} />
            </Routes>
          </Suspense>
        </div>
      </BrowserRouter>
    )
  }
}


const mapStateToProps = (state) => {
  return{
    initialized: state.app.initialized,
    profilePage: state.profilePage,
    messagePage: state.messagePage
  }
}

export default compose(connect(mapStateToProps, { initialize }))(App);
