import cn from "classnames";
import React from "react";
import { NavLink } from "react-router-dom";
import design from './Login.module.css';

class Login extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            isActive: false
        }
    }
    
    handleClick = () => {
        this.setState({
            isActive: !this.state.isActive
        })
    }

    render() {
        return (
            <>
                <div className={cn(design.login_button)}>{this.props.userLogin ? <NavLink to='/profile'>{this.props.userLogin}</NavLink> : <NavLink to='/login'>Login</NavLink>}</div>
                {this.props.userLogin ?
                    <>
                        <span onClick={this.handleClick} className={design.dropdown_button}><i className="fa fa-caret-down"></i></span>
                        <div className={cn(design.dropdown_menu)} style={{ display: this.state.isActive ? "block" : "none"}}>
                            <NavLink onClick={() => { this.props.logoutAccount() }}>Log out</NavLink>
                        </div>
                    </>
                    : ''
                }
            </>
        )
    }
}


export default Login;