const SEND_MESSAGE = "SEND_MESSAGE";
const SET_CURRENT_CHAT = "SET_CURRENT_CHAT";

let initialState = {
    senders: [
        { senderName: "Jake", senderId: 1 },
        { senderName: "Sonya", senderId: 2 },
        { senderName: "Pete", senderId: 3 }
    ],

    chatHistory: [
        {
            name: "Jake",
            content: [{ historyChatMessage: "Hi. How are you ?", senderMessageId: 1 },
            { historyChatMessage: "Fine, you ?", senderMessageId: 0 },
            { historyChatMessage: "I hope that you're ok ?", senderMessageId: 0 },
            { historyChatMessage: "Yeah, i am good", senderMessageId: 1 },
            { historyChatMessage: "Will you go with us at saturday ? ", senderMessageId: 1 },
            { historyChatMessage: "Where ?", senderMessageId: 0 },
            { historyChatMessage: "To cinema", senderMessageId: 1 },
            ]
        },
        {
            name: "Sonya",
            content: [{ historyChatMessage: "Do you alive ?", senderMessageId: 2 },
            { historyChatMessage: "Yea, why you are have this question ?", senderMessageId: 0 },
            { historyChatMessage: "It`s strange ;)", senderMessageId: 0 },
            { historyChatMessage: "Its mine instinct )", senderMessageId: 2 },
            ]
        },
        {
            name: "Pete",
            content: [
                { historyChatMessage: "What's up men", senderMessageId: 3 },
                { historyChatMessage: "Fine, you ?", senderMessageId: 0 },
                { historyChatMessage: "Men ?", senderMessageId: 0 },
                { historyChatMessage: "I am good", senderMessageId: 3 },
                { historyChatMessage: "But it's really cool", senderMessageId: 3 },
                { historyChatMessage: "What exactly ?", senderMessageId: 0 },
            ]
        },
    ],
    currentChatPerson: 'Jake'
}

export let dialogsReducer = (state = initialState, action) => {

    switch (action.type) {
        case SEND_MESSAGE:
            let newMessage = {
                historyChatMessage: action.newMessageText,
                senderMessageId: 0
            }
            return {
                ...state,
                chatHistory: state.chatHistory.map(obj =>
                    obj.name === action.currentPerson
                        ? { ...obj, content: [...obj.content, newMessage] }
                        : obj
                ),
                newMessagetext: ''
            }
        case SET_CURRENT_CHAT:
            return {
                ...state,
                currentChatPerson: action.person
            }

        default:
            return state;
    }
}

export const sendMessageActionCreator = (currentPerson, newMessageText) => ({ type: SEND_MESSAGE, currentPerson, newMessageText });
export const SetCurrentChatPage = (person) => ({ type: SET_CURRENT_CHAT, person })